import React, { useState, useEffect, useRef } from 'react';
import { 
  Typography, Box, Button, Paper, 
  Grid, FormControl, InputLabel, Select, MenuItem,
  CircularProgress, Alert, IconButton, Slider, Card,
  CardContent, TextField, Divider, Tooltip, Tabs, Tab
} from '@mui/material';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import AudioFileIcon from '@mui/icons-material/AudioFile';
import PlayCircleOutlineIcon from '@mui/icons-material/PlayCircleOutline';
import VolumeUpIcon from '@mui/icons-material/VolumeUp';
import StopIcon from '@mui/icons-material/Stop';
import DownloadIcon from '@mui/icons-material/Download';
import TuneIcon from '@mui/icons-material/Tune';
import MusicNoteIcon from '@mui/icons-material/MusicNote';
import SettingsVoiceIcon from '@mui/icons-material/SettingsVoice';
import BuildIcon from '@mui/icons-material/Build';
import GraphicEqIcon from '@mui/icons-material/GraphicEq';
import FolderZipIcon from '@mui/icons-material/FolderZip';
import { VoiceAPI } from '../../services/api';

// TabPanel component để hiển thị nội dung tab
function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`rvc-tabpanel-${index}`}
      aria-labelledby={`rvc-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 3 }}>
          {children}
        </Box>
      )}
    </div>
  );
}

const RVCTab = () => {
  // State cho tab navigation
  const [activeTab, setActiveTab] = useState(0);

  // State cho file audio input
  const [audioFile, setAudioFile] = useState(null);
  const [audioPreviewUrl, setAudioPreviewUrl] = useState(null);
  const [isSourcePlaying, setIsSourcePlaying] = useState(false);
  const [isResultPlaying, setIsResultPlaying] = useState(false);
  
  // State cho model RVC
  const [availableVoices, setAvailableVoices] = useState([]);
  const [selectedVoice, setSelectedVoice] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [modelLoading, setModelLoading] = useState(true);
  
  // State cho kết quả
  const [result, setResult] = useState(null);
  const [error, setError] = useState('');
  
  // State cho tham số RVC
  const [f0UpKey, setF0UpKey] = useState(0); // Điều chỉnh pitch
  const [indexRate, setIndexRate] = useState(0.5); // Tỷ lệ áp dụng index feature
  const [protectValue, setProtectValue] = useState(0.33); // Bảo vệ âm nổi
  const [rmsRate, setRmsRate] = useState(0.25); // Tỷ lệ RMS
  const [showAdvanced, setShowAdvanced] = useState(false);
  
  // State cho UVR5 (Tách giọng nói)
  const [uvrFile, setUvrFile] = useState(null);
  const [uvrResult, setUvrResult] = useState(null);
  const [vocalType, setVocalType] = useState('both');
  const [isUvrLoading, setIsUvrLoading] = useState(false);
  const [isVocalsPlaying, setIsVocalsPlaying] = useState(false);
  const [isInstrumentalPlaying, setIsInstrumentalPlaying] = useState(false);
  
  // State cho F0 extraction
  const [f0File, setF0File] = useState(null);
  const [f0Method, setF0Method] = useState('dio');
  const [f0Result, setF0Result] = useState(null);
  const [isF0Loading, setIsF0Loading] = useState(false);
  
  // State cho train model
  const [datasetFile, setDatasetFile] = useState(null);
  const [modelName, setModelName] = useState('');
  const [epochs, setEpochs] = useState(200);
  const [batchSize, setBatchSize] = useState(8);
  const [trainF0Method, setTrainF0Method] = useState('dio');
  const [isTraining, setIsTraining] = useState(false);
  const [trainResult, setTrainResult] = useState(null);
  
  // Refs cho audio players
  const sourceAudioRef = useRef(null);
  const resultAudioRef = useRef(null);
  const vocalsAudioRef = useRef(null);
  const instrumentalAudioRef = useRef(null);

  // Tải danh sách giọng nói RVC từ backend
  useEffect(() => {
    const fetchVoices = async () => {
      try {
        const response = await VoiceAPI.getModels();
        console.log('Kết quả API models:', response.data);
        
        if (response.data && response.data.rvc) {
          const rvcVoices = response.data.rvc;
          console.log(`Tìm thấy ${rvcVoices.length} mô hình RVC:`, rvcVoices);
          
          if (rvcVoices.length > 0) {
            setAvailableVoices(rvcVoices);
            setSelectedVoice(rvcVoices[0]);
            setError('');
          } else {
            console.warn('Không tìm thấy mô hình RVC nào');
            setError('Không tìm thấy mẫu giọng nói RVC nào. Vui lòng thêm mô hình vào thư mục ai/rvc/models.');
          }
        } else {
          console.error('Dữ liệu API không đúng định dạng:', response.data);
          setError('Định dạng dữ liệu không đúng. Không tìm thấy thông tin về mô hình RVC.');
        }
      } catch (err) {
        console.error('Lỗi khi tải danh sách giọng nói RVC:', err);
        setError('Không thể kết nối với máy chủ. Vui lòng thử lại sau.');
      } finally {
        setModelLoading(false);
      }
    };

    fetchVoices();
  }, []);

  // Xử lý khi người dùng chọn file
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac|ogg)$/)) {
        // Nếu đang phát âm thanh, dừng trước khi thay đổi file
        if (sourceAudioRef.current) {
          sourceAudioRef.current.pause();
          sourceAudioRef.current = null;
          setIsSourcePlaying(false);
        }
        
        // Giải phóng URL cũ nếu có
        if (audioPreviewUrl) {
          URL.revokeObjectURL(audioPreviewUrl);
        }
        
        setAudioFile(file);
        setError('');
        
        // Tạo URL cho file audio để nghe thử
        const fileUrl = URL.createObjectURL(file);
        setAudioPreviewUrl(fileUrl);
        
        // Reset kết quả khi chọn file mới
        setResult(null);
      } else {
        setError('Vui lòng chọn file âm thanh hợp lệ (WAV, MP3, FLAC, OGG)');
        setAudioFile(null);
        setAudioPreviewUrl(null);
      }
    }
  };

  // Xử lý khi người dùng chọn file cho UVR5
  const handleUvrFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac|ogg)$/)) {
        setUvrFile(file);
        setUvrResult(null);
        setError('');
      } else {
        setError('Vui lòng chọn file âm thanh hợp lệ (WAV, MP3, FLAC, OGG)');
        setUvrFile(null);
      }
    }
  };

  // Xử lý khi người dùng chọn giọng nói
  const handleVoiceChange = (event) => {
    setSelectedVoice(event.target.value);
  };

  // Xử lý khi người dùng nhấn nút chuyển đổi
  const handleConvert = async () => {
    if (!audioFile) {
      setError('Vui lòng chọn file âm thanh');
      return;
    }
    
    if (!selectedVoice) {
      setError('Vui lòng chọn giọng nói đích');
      return;
    }
    
    setIsLoading(true);
    setError('');
    setResult(null);
    
    try {
      // Tạo FormData để gửi lên server
      const formData = new FormData();
      formData.append('audio', audioFile);
      formData.append('model_type', 'rvc');
      formData.append('target_voice', selectedVoice);
      
      // Thêm các tham số nâng cao
      formData.append('f0up_key', f0UpKey.toString());
      formData.append('index_rate', indexRate.toString());
      formData.append('protect', protectValue.toString());
      formData.append('rms_mix_rate', rmsRate.toString());
      
      // Gửi request lên server
      const response = await VoiceAPI.convertVoice(formData);
      
      if (response.data.success) {
        setResult(response.data);
        console.log('Đã nhận kết quả:', response.data);
      } else {
        setError(`Chuyển đổi thất bại: ${response.data.error || 'Lỗi không xác định'}`);
      }
    } catch (err) {
      console.error('Lỗi khi chuyển đổi giọng nói:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Lỗi: ${err.response.data.error}`);
      } else {
        setError('Lỗi kết nối đến máy chủ. Vui lòng thử lại sau.');
      }
    } finally {
      setIsLoading(false);
    }
  };

  // Xử lý khi người dùng nhấn nút tách giọng nói
  const handleSeparateVocals = async () => {
    if (!uvrFile) {
      setError('Vui lòng chọn file âm thanh');
      return;
    }
    
    setIsUvrLoading(true);
    setError('');
    setUvrResult(null);
    
    try {
      // Gọi API tách giọng nói
      const response = await VoiceAPI.rvc.separateVocals(uvrFile, vocalType);
      
      if (response.data.success) {
        setUvrResult(response.data);
        console.log('Đã nhận kết quả UVR5:', response.data);
      } else {
        setError(`Tách giọng nói thất bại: ${response.data.error || 'Lỗi không xác định'}`);
      }
    } catch (err) {
      console.error('Lỗi khi tách giọng nói:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Lỗi: ${err.response.data.error}`);
      } else {
        setError('Lỗi kết nối đến máy chủ. Vui lòng thử lại sau.');
      }
    } finally {
      setIsUvrLoading(false);
    }
  };

  // Xử lý phát/dừng âm thanh gốc
  const handlePlaySourceAudio = () => {
    if (isSourcePlaying) {
      // Nếu đang phát thì dừng lại
      if (sourceAudioRef.current) {
        sourceAudioRef.current.pause();
        setIsSourcePlaying(false);
      }
    } else {
      // Nếu chưa phát thì tạo và phát
      const audio = new Audio(audioPreviewUrl);
      audio.onended = () => {
        setIsSourcePlaying(false);
        sourceAudioRef.current = null;
      };
      
      sourceAudioRef.current = audio;
      audio.play()
        .then(() => setIsSourcePlaying(true))
        .catch(error => {
          console.error('Lỗi khi phát âm thanh:', error);
          setError('Không thể phát âm thanh. Vui lòng thử lại.');
          setIsSourcePlaying(false);
        });
    }
  };

  // Xử lý phát/dừng âm thanh kết quả
  const handlePlayResultAudio = () => {
    if (!result || !result.result_url) {
      setError('Không có kết quả để phát');
      return;
    }
    
    if (isResultPlaying) {
      // Nếu đang phát thì dừng lại
      if (resultAudioRef.current) {
        resultAudioRef.current.pause();
        setIsResultPlaying(false);
      }
    } else {
      // Nếu chưa phát thì tạo và phát
      const audio = new Audio(result.result_url);
      audio.onended = () => {
        setIsResultPlaying(false);
        resultAudioRef.current = null;
      };
      
      resultAudioRef.current = audio;
      audio.play()
        .then(() => setIsResultPlaying(true))
        .catch(error => {
          console.error('Lỗi khi phát âm thanh kết quả:', error);
          setError('Không thể phát âm thanh kết quả. Vui lòng thử lại.');
          setIsResultPlaying(false);
        });
    }
  };

  // Xử lý phát/dừng file vocals
  const handlePlayVocals = () => {
    if (!uvrResult || !uvrResult.vocals_url) {
      setError('Không có file vocals để phát');
      return;
    }
    
    if (isVocalsPlaying) {
      if (vocalsAudioRef.current) {
        vocalsAudioRef.current.pause();
        setIsVocalsPlaying(false);
      }
    } else {
      const audio = new Audio(uvrResult.vocals_url);
      audio.onended = () => {
        setIsVocalsPlaying(false);
        vocalsAudioRef.current = null;
      };
      
      vocalsAudioRef.current = audio;
      audio.play()
        .then(() => setIsVocalsPlaying(true))
        .catch(error => {
          console.error('Lỗi khi phát file vocals:', error);
          setError('Không thể phát file vocals. Vui lòng thử lại.');
          setIsVocalsPlaying(false);
        });
    }
  };

  // Xử lý phát/dừng file instrumental
  const handlePlayInstrumental = () => {
    if (!uvrResult || !uvrResult.instrumental_url) {
      setError('Không có file instrumental để phát');
      return;
    }
    
    if (isInstrumentalPlaying) {
      if (instrumentalAudioRef.current) {
        instrumentalAudioRef.current.pause();
        setIsInstrumentalPlaying(false);
      }
    } else {
      const audio = new Audio(uvrResult.instrumental_url);
      audio.onended = () => {
        setIsInstrumentalPlaying(false);
        instrumentalAudioRef.current = null;
      };
      
      instrumentalAudioRef.current = audio;
      audio.play()
        .then(() => setIsInstrumentalPlaying(true))
        .catch(error => {
          console.error('Lỗi khi phát file instrumental:', error);
          setError('Không thể phát file instrumental. Vui lòng thử lại.');
          setIsInstrumentalPlaying(false);
        });
    }
  };

  // Xử lý tải xuống kết quả
  const handleDownload = (url) => {
    if (url) {
      VoiceAPI.downloadResult(url);
    } else {
      setError('Không có kết quả để tải xuống');
    }
  };

  // Xử lý chuyển đổi tab
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };
  
  // Xử lý trích xuất F0
  const handleExtractF0 = async () => {
    if (!f0File) {
      setError('Vui lòng chọn file âm thanh');
      return;
    }
    
    setIsF0Loading(true);
    setError('');
    setF0Result(null);
    
    try {
      // Gọi API trích xuất F0
      const response = await VoiceAPI.rvc.extractF0(f0File, f0Method);
      
      if (response.data.success) {
        setF0Result(response.data);
        console.log('Đã nhận kết quả F0:', response.data);
      } else {
        setError(`Trích xuất F0 thất bại: ${response.data.error || 'Lỗi không xác định'}`);
      }
    } catch (err) {
      console.error('Lỗi khi trích xuất F0:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Lỗi: ${err.response.data.error}`);
      } else {
        setError('Lỗi kết nối đến máy chủ. Vui lòng thử lại sau.');
      }
    } finally {
      setIsF0Loading(false);
    }
  };
  
  // Xử lý khi người dùng chọn file cho F0
  const handleF0FileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type.includes('audio') || file.name.match(/\.(wav|mp3|flac|ogg)$/)) {
        setF0File(file);
        setF0Result(null);
        setError('');
      } else {
        setError('Vui lòng chọn file âm thanh hợp lệ (WAV, MP3, FLAC, OGG)');
        setF0File(null);
      }
    }
  };
  
  // Xử lý khi người dùng chọn dataset cho train model
  const handleDatasetFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      if (file.type === 'application/zip' || file.name.endsWith('.zip')) {
        setDatasetFile(file);
        setTrainResult(null);
        setError('');
      } else {
        setError('Vui lòng chọn file ZIP chứa dataset');
        setDatasetFile(null);
      }
    }
  };
  
  // Xử lý train model
  const handleTrainModel = async () => {
    if (!datasetFile) {
      setError('Vui lòng chọn file dataset');
      return;
    }
    
    if (!modelName) {
      setError('Vui lòng nhập tên model');
      return;
    }
    
    setIsTraining(true);
    setError('');
    setTrainResult(null);
    
    try {
      // Gọi API train model
      const response = await VoiceAPI.rvc.trainModel(
        datasetFile, 
        modelName,
        epochs,
        batchSize,
        trainF0Method
      );
      
      if (response.data.success) {
        setTrainResult(response.data);
        console.log('Đã nhận kết quả train model:', response.data);
      } else {
        setError(`Huấn luyện model thất bại: ${response.data.error || 'Lỗi không xác định'}`);
      }
    } catch (err) {
      console.error('Lỗi khi huấn luyện model:', err);
      if (err.response && err.response.data && err.response.data.error) {
        setError(`Lỗi: ${err.response.data.error}`);
      } else {
        setError('Lỗi kết nối đến máy chủ. Vui lòng thử lại sau.');
      }
    } finally {
      setIsTraining(false);
    }
  };

  return (
    <Box sx={{ p: 2 }}>
      <Paper elevation={2} sx={{ p: 3, mb: 3 }}>
        <Tabs 
          value={activeTab} 
          onChange={handleTabChange} 
          aria-label="RVC options tabs"
          variant="fullWidth"
        >
          <Tab icon={<SettingsVoiceIcon />} label="Chuyển đổi" id="rvc-tab-0" />
          <Tab icon={<MusicNoteIcon />} label="UVR5" id="rvc-tab-1" />
          <Tab icon={<GraphicEqIcon />} label="F0" id="rvc-tab-2" />
          <Tab icon={<BuildIcon />} label="Train Model" id="rvc-tab-3" />
        </Tabs>
        
        {/* Tab 1: Chuyển đổi giọng nói */}
        <TabPanel value={activeTab} index={0}>
          <Typography variant="h6" gutterBottom>
            Chuyển đổi giọng nói với RVC
          </Typography>
          
          {/* Chọn file âm thanh */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chọn file âm thanh
                <input
                  type="file"
                  accept="audio/*"
                  hidden
                  onChange={handleFileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {audioFile ? (
                  <>
                    <AudioFileIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {audioFile.name}
                    </Typography>
                    <IconButton 
                      onClick={handlePlaySourceAudio}
                      disabled={!audioPreviewUrl}
                      color="primary"
                    >
                      {isSourcePlaying ? <StopIcon /> : <PlayCircleOutlineIcon />}
                    </IconButton>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Chưa chọn file nào
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Chọn giọng nói đích */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Giọng nói đích</InputLabel>
            <Select
              value={selectedVoice}
              onChange={handleVoiceChange}
              label="Giọng nói đích"
              disabled={modelLoading}
            >
              {modelLoading ? (
                <MenuItem value="">Đang tải...</MenuItem>
              ) : (
                availableVoices.map((voice) => (
                  <MenuItem key={voice} value={voice}>
                    {voice}
                  </MenuItem>
                ))
              )}
            </Select>
          </FormControl>
          
          {/* Điều chỉnh Pitch (f0up_key) */}
          <Typography gutterBottom>
            Điều chỉnh độ cao (Pitch)
          </Typography>
          <Grid container spacing={2} alignItems="center">
            <Grid item xs>
              <Slider
                value={f0UpKey}
                onChange={(e, newValue) => setF0UpKey(newValue)}
                min={-12}
                max={12}
                step={1}
                marks
                valueLabelDisplay="auto"
              />
            </Grid>
            <Grid item>
              <Typography variant="body2">
                {f0UpKey > 0 ? `+${f0UpKey}` : f0UpKey}
              </Typography>
            </Grid>
          </Grid>
          
          {/* Nút hiển thị tham số nâng cao */}
          <Box sx={{ mt: 2, mb: 1 }}>
            <Button 
              startIcon={<TuneIcon />}
              onClick={() => setShowAdvanced(!showAdvanced)}
              size="small"
            >
              {showAdvanced ? 'Ẩn tham số nâng cao' : 'Hiển thị tham số nâng cao'}
            </Button>
          </Box>
          
          {/* Tham số nâng cao */}
          {showAdvanced && (
            <Card variant="outlined" sx={{ mt: 1, mb: 2 }}>
              <CardContent>
                <Typography variant="subtitle2" gutterBottom>
                  Tham số nâng cao
                </Typography>
                
                {/* Index Rate */}
                <Typography variant="body2" gutterBottom>
                  Tỷ lệ áp dụng index (Index Rate)
                </Typography>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs>
                    <Slider
                      value={indexRate}
                      onChange={(e, newValue) => setIndexRate(newValue)}
                      min={0}
                      max={1}
                      step={0.01}
                      valueLabelDisplay="auto"
                    />
                  </Grid>
                  <Grid item>
                    <Typography variant="body2">
                      {indexRate.toFixed(2)}
                    </Typography>
                  </Grid>
                </Grid>
                
                {/* Protect */}
                <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                  Bảo vệ âm nổi (Protect)
                </Typography>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs>
                    <Slider
                      value={protectValue}
                      onChange={(e, newValue) => setProtectValue(newValue)}
                      min={0}
                      max={0.5}
                      step={0.01}
                      valueLabelDisplay="auto"
                    />
                  </Grid>
                  <Grid item>
                    <Typography variant="body2">
                      {protectValue.toFixed(2)}
                    </Typography>
                  </Grid>
                </Grid>
                
                {/* RMS Mix Rate */}
                <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                  Tỷ lệ trộn RMS
                </Typography>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs>
                    <Slider
                      value={rmsRate}
                      onChange={(e, newValue) => setRmsRate(newValue)}
                      min={0}
                      max={1}
                      step={0.01}
                      valueLabelDisplay="auto"
                    />
                  </Grid>
                  <Grid item>
                    <Typography variant="body2">
                      {rmsRate.toFixed(2)}
                    </Typography>
                  </Grid>
                </Grid>
              </CardContent>
            </Card>
          )}
          
          {/* Nút chuyển đổi */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleConvert}
            disabled={isLoading || !audioFile || !selectedVoice}
          >
            {isLoading ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                Đang xử lý...
              </>
            ) : (
              'Chuyển đổi giọng nói'
            )}
          </Button>
          
          {/* Kết quả */}
          {result && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kết quả chuyển đổi
              </Typography>
              
              <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Typography>
                  Đã chuyển đổi thành công giọng nói sang "{result.target_voice}"
                </Typography>
                
                <Box>
                  <IconButton color="primary" onClick={handlePlayResultAudio}>
                    {isResultPlaying ? <StopIcon /> : <VolumeUpIcon />}
                  </IconButton>
                  
                  <IconButton color="primary" onClick={() => handleDownload(result.result_url)}>
                    <DownloadIcon />
                  </IconButton>
                </Box>
              </Box>
              
              {/* Thông tin chi tiết */}
              <Box sx={{ mt: 2 }}>
                <Divider sx={{ my: 1 }} />
                <Typography variant="body2">
                  <strong>Model:</strong> RVC
                </Typography>
                <Typography variant="body2">
                  <strong>Độ cao (Pitch):</strong> {f0UpKey > 0 ? `+${f0UpKey}` : f0UpKey}
                </Typography>
                {showAdvanced && (
                  <>
                    <Typography variant="body2">
                      <strong>Index Rate:</strong> {indexRate.toFixed(2)}
                    </Typography>
                    <Typography variant="body2">
                      <strong>Protect:</strong> {protectValue.toFixed(2)}
                    </Typography>
                    <Typography variant="body2">
                      <strong>RMS Mix Rate:</strong> {rmsRate.toFixed(2)}
                    </Typography>
                  </>
                )}
              </Box>
            </Paper>
          )}
        </TabPanel>
        
        {/* Tab 2: UVR5 - Tách giọng nói */}
        <TabPanel value={activeTab} index={1}>
          <Typography variant="h6" gutterBottom>
            Tách giọng nói và nhạc nền (UVR5)
          </Typography>
          
          {/* Chọn file âm thanh */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chọn file âm thanh
                <input
                  type="file"
                  accept="audio/*"
                  hidden
                  onChange={handleUvrFileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {uvrFile ? (
                  <>
                    <AudioFileIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {uvrFile.name}
                    </Typography>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Chưa chọn file nào
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Chọn loại tách */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Loại tách</InputLabel>
            <Select
              value={vocalType}
              onChange={(e) => setVocalType(e.target.value)}
              label="Loại tách"
            >
              <MenuItem value="vocals">Chỉ tách giọng nói</MenuItem>
              <MenuItem value="instrumental">Chỉ tách nhạc nền</MenuItem>
              <MenuItem value="both">Tách cả hai</MenuItem>
            </Select>
          </FormControl>
          
          {/* Nút tách giọng nói */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleSeparateVocals}
            disabled={isUvrLoading || !uvrFile}
          >
            {isUvrLoading ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                Đang xử lý...
              </>
            ) : (
              'Tách giọng nói'
            )}
          </Button>
          
          {/* Kết quả UVR5 */}
          {uvrResult && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kết quả tách giọng nói
              </Typography>
              
              {/* Hiển thị vocals nếu có */}
              {uvrResult.vocals_url && (
                <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 1, bgcolor: 'background.paper', borderRadius: 1 }}>
                  <Typography>
                    <strong>Giọng nói (Vocals)</strong>
                  </Typography>
                  
                  <Box>
                    <IconButton color="primary" onClick={handlePlayVocals}>
                      {isVocalsPlaying ? <StopIcon /> : <VolumeUpIcon />}
                    </IconButton>
                    
                    <IconButton color="primary" onClick={() => handleDownload(uvrResult.vocals_url)}>
                      <DownloadIcon />
                    </IconButton>
                  </Box>
                </Box>
              )}
              
              {/* Hiển thị instrumental nếu có */}
              {uvrResult.instrumental_url && (
                <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between', p: 1, bgcolor: 'background.paper', borderRadius: 1 }}>
                  <Typography>
                    <strong>Nhạc nền (Instrumental)</strong>
                  </Typography>
                  
                  <Box>
                    <IconButton color="primary" onClick={handlePlayInstrumental}>
                      {isInstrumentalPlaying ? <StopIcon /> : <VolumeUpIcon />}
                    </IconButton>
                    
                    <IconButton color="primary" onClick={() => handleDownload(uvrResult.instrumental_url)}>
                      <DownloadIcon />
                    </IconButton>
                  </Box>
                </Box>
              )}
            </Paper>
          )}
        </TabPanel>
        
        {/* Tab 3: Trích xuất F0 */}
        <TabPanel value={activeTab} index={2}>
          <Typography variant="h6" gutterBottom>
            Trích xuất đường cong F0
          </Typography>
          
          {/* Chọn file âm thanh */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<CloudUploadIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chọn file âm thanh
                <input
                  type="file"
                  accept="audio/*"
                  hidden
                  onChange={handleF0FileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {f0File ? (
                  <>
                    <AudioFileIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {f0File.name}
                    </Typography>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Chưa chọn file nào
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Chọn phương pháp F0 */}
          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>Phương pháp trích xuất F0</InputLabel>
            <Select
              value={f0Method}
              onChange={(e) => setF0Method(e.target.value)}
              label="Phương pháp trích xuất F0"
            >
              <MenuItem value="dio">DIO (Nhanh)</MenuItem>
              <MenuItem value="harvest">Harvest (Chính xác)</MenuItem>
              <MenuItem value="crepe">Crepe (Dùng GPU, tốt nhất)</MenuItem>
            </Select>
          </FormControl>
          
          {/* Nút trích xuất F0 */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleExtractF0}
            disabled={isF0Loading || !f0File}
          >
            {isF0Loading ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                Đang xử lý...
              </>
            ) : (
              'Trích xuất F0'
            )}
          </Button>
          
          {/* Kết quả F0 */}
          {f0Result && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kết quả trích xuất F0
              </Typography>
              
              <Box sx={{ mt: 2, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                <Typography>
                  Đã trích xuất F0 thành công
                </Typography>
                
                <Button
                  variant="outlined"
                  startIcon={<DownloadIcon />}
                  onClick={() => handleDownload(f0Result.f0_url)}
                >
                  Tải xuống F0 (.npy)
                </Button>
              </Box>
            </Paper>
          )}
        </TabPanel>
        
        {/* Tab 4: Huấn luyện Model */}
        <TabPanel value={activeTab} index={3}>
          <Typography variant="h6" gutterBottom>
            Huấn luyện Model RVC mới
          </Typography>
          
          <Alert severity="info" sx={{ mb: 2 }}>
            Upload file ZIP chứa các file âm thanh để huấn luyện model giọng nói mới.
            Dataset nên có ít nhất 5-10 phút giọng nói chất lượng tốt.
          </Alert>
          
          {/* Chọn file dataset */}
          <Grid container spacing={2} sx={{ mt: 1, mb: 2 }}>
            <Grid item xs={12} md={6}>
              <Button
                variant="outlined"
                component="label"
                startIcon={<FolderZipIcon />}
                fullWidth
                sx={{ height: '56px' }}
              >
                Chọn file ZIP dataset
                <input
                  type="file"
                  accept=".zip"
                  hidden
                  onChange={handleDatasetFileChange}
                />
              </Button>
            </Grid>
            <Grid item xs={12} md={6}>
              <Paper variant="outlined" sx={{ p: 1, display: 'flex', alignItems: 'center', height: '56px' }}>
                {datasetFile ? (
                  <>
                    <FolderZipIcon color="primary" sx={{ mx: 1 }} />
                    <Typography variant="body2" sx={{ flex: 1, overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
                      {datasetFile.name}
                    </Typography>
                  </>
                ) : (
                  <Typography variant="body2" color="textSecondary" sx={{ mx: 1 }}>
                    Chưa chọn file nào
                  </Typography>
                )}
              </Paper>
            </Grid>
          </Grid>
          
          {/* Nhập tên model */}
          <TextField
            fullWidth
            label="Tên Model"
            value={modelName}
            onChange={(e) => setModelName(e.target.value)}
            margin="normal"
            variant="outlined"
            helperText="Đặt tên cho model của bạn"
          />
          
          {/* Tham số huấn luyện */}
          <Card variant="outlined" sx={{ mt: 2, mb: 2 }}>
            <CardContent>
              <Typography variant="subtitle2" gutterBottom>
                Tham số huấn luyện
              </Typography>
              
              {/* Epochs */}
              <Typography variant="body2" gutterBottom>
                Số epochs
              </Typography>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs>
                  <Slider
                    value={epochs}
                    onChange={(e, newValue) => setEpochs(newValue)}
                    min={50}
                    max={500}
                    step={10}
                    valueLabelDisplay="auto"
                  />
                </Grid>
                <Grid item>
                  <Typography variant="body2">
                    {epochs}
                  </Typography>
                </Grid>
              </Grid>
              
              {/* Batch size */}
              <Typography variant="body2" gutterBottom sx={{ mt: 1 }}>
                Batch size
              </Typography>
              <Grid container spacing={2} alignItems="center">
                <Grid item xs>
                  <Slider
                    value={batchSize}
                    onChange={(e, newValue) => setBatchSize(newValue)}
                    min={1}
                    max={16}
                    step={1}
                    valueLabelDisplay="auto"
                  />
                </Grid>
                <Grid item>
                  <Typography variant="body2">
                    {batchSize}
                  </Typography>
                </Grid>
              </Grid>
              
              {/* Phương pháp F0 */}
              <FormControl fullWidth sx={{ mt: 2 }}>
                <InputLabel>Phương pháp F0</InputLabel>
                <Select
                  value={trainF0Method}
                  onChange={(e) => setTrainF0Method(e.target.value)}
                  label="Phương pháp F0"
                >
                  <MenuItem value="dio">DIO (Nhanh)</MenuItem>
                  <MenuItem value="harvest">Harvest (Chính xác)</MenuItem>
                  <MenuItem value="crepe">Crepe (Dùng GPU, tốt nhất)</MenuItem>
                </Select>
              </FormControl>
            </CardContent>
          </Card>
          
          {/* Nút huấn luyện */}
          <Button
            variant="contained"
            fullWidth
            sx={{ mt: 2 }}
            onClick={handleTrainModel}
            disabled={isTraining || !datasetFile || !modelName}
          >
            {isTraining ? (
              <>
                <CircularProgress size={24} sx={{ mr: 1 }} color="inherit" />
                Đang huấn luyện... (Có thể mất nhiều phút)
              </>
            ) : (
              'Huấn luyện Model'
            )}
          </Button>
          
          {/* Kết quả train model */}
          {trainResult && (
            <Paper elevation={1} sx={{ p: 2, mt: 3 }}>
              <Typography variant="h6" gutterBottom>
                Kết quả huấn luyện model
              </Typography>
              
              <Box sx={{ mt: 2 }}>
                <Alert severity="success">
                  Đã huấn luyện model thành công!
                </Alert>
                
                <Typography variant="body1" sx={{ mt: 2 }}>
                  Model: <strong>{trainResult.model_name}</strong>
                </Typography>
                
                <Typography variant="body2" sx={{ mt: 1 }}>
                  Model đã được lưu và sẵn sàng sử dụng. Bạn có thể sử dụng model này trong tab chuyển đổi giọng nói.
                </Typography>
              </Box>
            </Paper>
          )}
        </TabPanel>
      </Paper>
      
      {/* Thông báo lỗi chung */}
      {error && (
        <Alert severity="error" sx={{ mt: 2 }}>
          {error}
        </Alert>
      )}
    </Box>
  );
};

export default RVCTab; 